import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, DateField } from 'src/app/common-module/table-list/list-field-type';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';
import { ActivatedRoute } from '@angular/router';
import { ISubscription } from "rxjs/Subscription";
import { DataService } from 'src/app/service/data.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-sorted-list',
  templateUrl: './sorted-list.component.html'
})
export class SortedListComponent implements OnInit {


  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  productDetials: any;
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];
  orderId: string;
  productId: string;
  private subscription: ISubscription;
  orderDetais: any;

  constructor(
    private paginationService: PaginationService,
    private dataService: DataService,
    private location: Location,
    private activatedRoute: ActivatedRoute,
    private productregistrationService: ProductRegistrationService,
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('order_id') && this.activatedRoute.snapshot.paramMap.get('product_id')) {
      this.orderId = this.activatedRoute.snapshot.paramMap.get('order_id');
      this.productId = this.activatedRoute.snapshot.paramMap.get('product_id');
    }
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.orderDetais = data;
      }
    });
  }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Sorted Qty",
        key: "sorted_qty",
      }),
      new TextField({
        label: "Tag",
        key: "tag.label",
      }),

    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/inbound/registeration/edit/',
        id: 'product_register_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'sorting/',
        id: 'product_register_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.productregistrationService.getSortedListById(this.productId, this.orderId, params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list ? response.data.list : [];
        this.productDetials = response.data.product;
        this.showPagination = true;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  back() {
    this.location.back();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}




