import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RegisterationComponent } from './registeration.component';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { SortedListComponent } from './sorted-list/sorted-list.component';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';

const routes: Routes = [
  {
    path: '',
    component: RegisterationComponent,
    data: { title: 'product_register_list' },
  },
  {
    path: 'sorted-qty',
    component: SortedListComponent,
    data: { title: 'product_register_list' },
  },

  // {
  //   path: 'add',
  //   loadChildren: () => import('./add-registeration/add-registeration.module').then(m => m.AddRegisterationModule),
  //   data: { title: 'add_product_register' },
  // },
  {
    path: 'add',
    loadChildren: () => import('./add-configuration/add-configuration.module').then(m => m.AddConfigurationModule),
    data: { title: 'add_product_register' },
  },
  {
    path: 'edit',
    loadChildren: () => import('./add-configuration/add-configuration.module').then(m => m.AddConfigurationModule),
    data: { title: 'edit_product_register' },
  },
  {
    path: 'register-product',
    loadChildren: () => import('./add-configuration/add-configuration.module').then(m => m.AddConfigurationModule),
    data: { title: 'add_product_register' },
  },
  // {
  //   path: 'edit',
  //   loadChildren: () => import('./add-registeration/add-registeration.module').then(m => m.AddRegisterationModule),
  //   data: { title: 'edit_product_register' },
  // }

]


@NgModule({
  declarations: [RegisterationComponent,SortedListComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    PaginationModule,
    SharedModule,
  ], providers: [ProductRegistrationService]
})
export class RegisterationModule { }
